.container {
  border-radius: 10px;
  width: min(100% - 20px, 100%);
  margin: 0 10px 0 10px;
  display: flex;
  align-items: stretch;
  flex-direction: column;
  max-width: 400px;
  z-index: 1;
}

.inputContainer {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column-reverse;
  align-items: center;
  align-content: center;
  user-select: none;
}
