.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: min(100%, 100%);
}
.list {
  margin: 10px 0 10px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  max-width: auto;
  border-radius: 20px;
  z-index: 1;
}

.text {
  margin: 0 0px 0 20px;
  align-self: center;
  font-weight: 700;
  font-size: 13px;
}

.taskInput {
  flex: 1;
  width: min(100% - 5px, 200px);
  margin: -10px 0 10px 0;
  font-size: 14px;
}

.taskInput:focus {
  margin: 0 0 0 0;
}

.taskDesc {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: min(100% - 15px, 300px);
  cursor: pointer;
}

.taskDesc::before {
  content: "";
  position: absolute;
  left: 5%;
  bottom: 45%;
  width: min(100% - 10px, 100%);
  max-width: 270px;
  height: 2px; /* Line thickness */
  background-color: red; /* Line color */
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 300ms ease-in-out;
}

.taskInfo {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: min(100% - 15px, 430px);
  max-width: 700px;
}

.taskInputInfo {
  display: flex;
  width: min(100% - 15px, 473px);
  justify-content: center;
  max-width: 600px;
  flex-wrap: wrap;
  font-weight: bolder;
}

.listDone .taskDesc {
  color: gray;
}

.listDone .taskDesc::before {
  transform: scaleX(1);
}

