@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  transition: background-color 300ms ease-in-out, color 300ms ease-in-out;
}

/* Default light mode styles */
body {
  @apply bg-sky-200 text-black;
}

/* Dark mode styles */
.dark body {
  @apply bg-gray-900 text-white;
}

.starry-background {
  position: relative;
  height: 100vh;
  overflow: auto; /* Prevents layout shifting due to stars */
  padding: 0;
}

/* Hide scrollbar for Webkit browsers (Chrome, Safari) */
.starry-background::-webkit-scrollbar {
  width: 0;
  height: 0; /* Hide vertical scrollbar too */
}

/* Hide scrollbar for Firefox */
.starry-background {
  scrollbar-width: none;
}

/* Hide scrollbar for IE and Edge */
.starry-background {
  -ms-overflow-style: none;
}

.star {
  position: absolute;
  background-color: #fff;
  border-radius: 50%;
  opacity: 0.7;
  transition: background-color 300ms ease-in-out;
  animation: twinkle 1.3s infinite;
}

@keyframes twinkle {
  0%,
  100% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
}
